.about-us {
  row-gap: 32px;

  @include page-grid;

  &__title {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__text {
    grid-column: 1 / -1;
    font-size: 23px;
    font-weight: 500;
    line-height: 32.2px;
    margin-block: 0;

    @include on-tablet {
      grid-column: 4 / 7;
    }

    @include on-desktop {
      grid-column: span 6;
    }
  }
}
